import {
  Button, Checkbox, FormControlLabel, FormGroup, TextField,
} from '@mui/material';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { Colors } from '../common/colors';

const StyledContainer = styled.div`
  width: 100%;
  height: 100vh;
  padding-top: 48px;
  padding-bottom: 48px;
  display: flex;
  justify-content: center;
  align-items: center;

  .checkboxes {
    margin-bottom: 24px;
  }
  
  .form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .error {
    margin-top: 8px;
    font-size: 14px;
    color: ${Colors.error};
  }

  .button {
    margin-top: 24px;
  }
`;

function Home() {
  const navigate = useNavigate();
  const [names, setNames] = useState('');
  const [error, setError] = useState(false);
  const [state, setState] = useState({ production: true, qa: true, weekly: false });

  const handleChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    setError(false);
    setNames(event.target.value);
  };

  const onChangeCheckbox = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, checked } = event.target;

    setState((prevState) => ({ ...prevState, [name]: checked }));
  };

  const onSubmit = () => {
    if (!names) {
      setError(true);
      return;
    }

    navigate('/daily', { state: { names, ...state } });
  };

  return (
    <StyledContainer>
      <div className="form">
        <FormGroup className="checkboxes">
          <FormControlLabel
            control={<Checkbox name="production" checked={state.production} onChange={onChangeCheckbox} />}
            label="Production"
          />
          <FormControlLabel
            control={<Checkbox name="qa" checked={state.qa} onChange={onChangeCheckbox} />}
            label="QA"
          />
          <FormControlLabel
            control={<Checkbox name="weekly" checked={state.weekly} onChange={onChangeCheckbox} />}
            label="Weekly"
          />
        </FormGroup>
        <TextField
          error={error}
          fullWidth
          label="Names"
          minRows={3}
          multiline
          onChange={handleChange}
          variant="outlined"
        />
        { error && <div className="error">Cannot be blank</div> }
        <Button className="button" variant="contained" onClick={onSubmit}>Start daily</Button>
      </div>
    </StyledContainer>
  );
}

export default Home;
