import React, { FunctionComponent } from 'react';
import styled from 'styled-components';
import ImageIcon from '@mui/icons-material/Image';
import { Colors } from '../common/colors';

type Props = {
  personPassing?: string;
};

const StyledContainer = styled.div`
  width: 100%;
  padding-bottom: 32px;
  margin-top: 32px;
  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  .avatar {
    background-color: ${Colors.beige};
    width: 200px;
    height: 200px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;

export const PersonToPass: FunctionComponent<Props> = ({ personPassing }: Props) => (
  <StyledContainer>
    <h2>Person passing</h2>
    <div className="avatar">
      {!personPassing
        ? <ImageIcon />
        : <img src={`${process.env.PUBLIC_URL}/photos/${personPassing.toLowerCase()}-glab.png`} alt={personPassing} />}
    </div>
  </StyledContainer>
);
