import {
  Avatar,
  Button, Checkbox, colors, FormControlLabel, FormGroup, List, ListItem, ListItemAvatar, ListItemText,
} from '@mui/material';
import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { PersonToPass } from '../components/PersonToPass';

const StyledContainer = styled.div`
  width: 100%;
  padding-top: 32px;

  .button {
    margin-top: 24px;
  }

  .list {
    border-radius: 8px;
    border: 1px solid ${colors.grey[500]};
    max-height: 250px;
    overflow-y: auto;
  }

  img {
    max-width: 100%;
    max-height: 100%;
  }
`;

type State = {
  names: string;
  production: boolean;
  qa: boolean;
  weekly: boolean;
}

function Daily() {
  const [state, setState] = useState({ production: false, qa: false, weekly: false });
  const location = useLocation();

  const {
    names, production, qa, weekly,
  } = location.state as State;

  const onChangeCheckbox = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, checked } = event.target;

    setState((prevState) => ({ ...prevState, [name]: checked }));
  };

  const allNames = names.split(/\r?\n/).filter((name) => !!name).map((name, index) => ({
    id: index,
    name,
  }));

  const [peopleToPass, setPeopleToPass] = useState(allNames);
  const [personPassing, setPersonPassing] = useState('');

  const onClick = () => {
    const [person] = peopleToPass.splice(Math.floor(Math.random() * peopleToPass.length), 1);
    setPeopleToPass(peopleToPass);
    setPersonPassing(person.name);
  };

  return (
    <StyledContainer>
      {(production || qa || weekly)
        && (
          <FormGroup className="checkboxes">
            {production && (
            <FormControlLabel
              control={<Checkbox name="production" checked={state.production} onChange={onChangeCheckbox} />}
              label="Production"
            />
            )}
            {qa && (
            <FormControlLabel
              control={<Checkbox name="qa" checked={state.qa} onChange={onChangeCheckbox} />}
              label="QA"
            />
            )}
            {weekly && (
            <FormControlLabel
              control={<Checkbox name="weekly" checked={state.weekly} onChange={onChangeCheckbox} />}
              label="Weekly"
            />
            )}
          </FormGroup>
        )}
      <PersonToPass personPassing={personPassing} />
      {peopleToPass.length > 0 && (
        <>
          <h2>People to pass</h2>
          <List className="list">
            {peopleToPass.map((person) => (
              <ListItem key={person.id}>
                <ListItemAvatar>
                  <Avatar>
                    <img
                      src={`${process.env.PUBLIC_URL}/photos/${person.name.toLowerCase()}-glab.png`}
                      alt={person.name}
                    />
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary={person.name} />
              </ListItem>
            ))}
          </List>
          <Button className="button" variant="contained" onClick={onClick}>
            Draw randomly
          </Button>
        </>
      )}
    </StyledContainer>
  );
}

export default Daily;
